import React, { useContext, useEffect } from "react";
import Notes from "./Notes/Notes";
import NoteContext from "../context/note/NoteContext";
import { useState } from "react";
import Alert from './Alert'
import { useHistory } from "react-router";
import { useSelector } from "react-redux";
const Home = () => {
    const location = useHistory();
    const context = useContext(NoteContext)
    const { addNote, getNotes } = context

    const [note, setnote] = useState({ title: "", descrption: "", tag: "" })
    const [state, setstate] = useState(0)
    const addbutton = (e) => {
        e.preventDefault();
        addNote(note.title, note.descrption, note.tag)
        setstate(state + 1)
    }
    const handleFunc = (e) => {

        setnote({ ...note, [e.target.name]: e.target.value })

    }

    useEffect(() => {

        if (localStorage.getItem('token')) {
            getNotes()

        }
        else {
            location.push("/login")
        }


    }, [state])

    const alert_msg = useSelector(state => state.alert_msg)
    const alert_val = useSelector(state => state.alert_val)

    return <>

        <div className="mb-3 my-3">
            {alert_val ? <Alert message={alert_msg} /> : ""}

            <h2>Add a Note</h2>
            <label htmlFor="title" className="form-label">Title</label>
            <input type="text" className="form-control" id="title" name="title" placeholder="This is My Note" onChange={handleFunc} />
        </div>
        <div className="mb-3">
            <label htmlFor="descrption" className="form-label">Description</label>
            <textarea className="form-control" id="descrption" name="descrption" rows="3" onChange={handleFunc}></textarea>
        </div>
        <div className="mb-3">
            <label htmlFor="tag" className="form-label">Add Tag</label>
            <input type="text" className="form-control" id="tag" name="tag" placeholder="Personal" onChange={handleFunc} />
        </div>
        <div className="col-12 my-3">
            <button disabled={note.descrption.length < 5 || note.title.length < 3} type="submit" className="btn btn-primary" onClick={addbutton}>Add Note</button>
        </div>
        <div className="my-3">
            <h2>Your Note Are:</h2>
            <Notes />
        </div>



    </>
}

export default Home;