import React, { useContext, useState } from 'react'
import userContext from '../context/user/UserContext';
import Alert from '../components/Alert'
import { useSelector } from 'react-redux';
const Signup = () => {
  const [credtials, setcredials] = useState({ name:"",email: "", password: "" })
  const context = useContext(userContext)
  const { signup_func,salert } = context
  const handle=(e)=>{
     e.preventDefault();
     signup_func(credtials.name,credtials.email,credtials.password)
  }

  const onchange = (e) => {
    setcredials({ ...credtials, [e.target.name]: e.target.value })
}
const alert_msg = useSelector(state => state.alert_msg)
const alert_val = useSelector(state => state.alert_val)

  return (
    <>
      <div className="container my-3">
      {alert_val?<Alert message={alert_msg}/>:""}
           
      <form onSubmit={handle} className="px-4 py-3">
        <div class="form-group">
          <label for="name">Enter Your Name</label>
          <input type="text" class="form-control" id="name" placeholder="Enter Your Name" name="name" onChange={onchange} />
        </div>
        <div class="form-group">
          <label for="email">Email address</label>
          <input type="email" class="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" name="email" onChange={onchange} />
          <small id="email" class="form-text text-muted">We'll never share your email with anyone else.</small>
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input type="password" class="form-control" id="password" placeholder="Password" name="password" onChange={onchange} />
        </div>
        <div class="form-group my-3">
          <button type="submit" class="btn btn-primary">Submit</button>
        </div>
     
      </form>
      </div>   
    </>
  )
}

export default Signup
