import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import React, { useContext, useState } from 'react'
import NoteContext from '../../context/note/NoteContext'
import NewsItem from './NewsItem'
function Notes() {

    const context = useContext(NoteContext)
    const { notes, edit_notes } = context

    const [show, setShow] = useState(false);
    const [note, setnote] = useState({ title: "", descrption: "", tag: "" })


    const update_notes = (fetch_note) => {

        if (show === false) {
            setShow(true);
        } else {
            setShow(false);
        }
        setnote(fetch_note)

    }
    const addbutton = (e) => {
        e.preventDefault();
        setShow(false)
        edit_notes(note._id, note.title, note.descrption, note.tag)

    }
    const handleFunc = (e) => {
        setnote({ ...note, [e.target.name]: e.target.value })


    }



    return (

        <div className="row my-3">

            <Modal show={show} onHide={() => { setShow(false) }}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit Note</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="mb-3 my-3">
                        <label htmlFor="etitle" className="form-label">Title</label>
                        <input type="text" className="form-control" id="etitle" name="title" onChange={handleFunc} value={note.title} />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="edescrption" className="form-label">Description</label>
                        <textarea className="form-control" id="descrption" name="descrption" rows="3" onChange={handleFunc} value={note.descrption}></textarea>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="tag" className="form-label">Add Tag</label>
                        <input type="text" className="form-control" id="etag" name="tag" onChange={handleFunc} value={note.tag} />
                    </div>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() => { setShow(false) }}>
                        Close
                    </Button>
                    <Button disabled={note.descrption.length < 5 || note.title.length < 3} variant="primary" onClick={addbutton}>
                        Edit Note
                    </Button>
                </Modal.Footer>
            </Modal>
            <div className="container">
                {notes.length === 0 && 'No Notes To Display'}
            </div>
            {notes.map((note) => {
                return <NewsItem  update_notes={update_notes} notes={note} key={note._id} />
            })}
        </div>
    )
}

export default Notes
