import React from "react";
import {NavLink} from 'react-router-dom'
import { useSelector } from "react-redux";
import './index.css'
const Menu=()=>{
  const state = useSelector(state => state.token)
    return <>
     <nav className="navbar">
     <div className="logo">I-NoteBook</div>
     <ul className="nav-links">
       <div className="menu">
       <li><NavLink exact activeClassName="active_op" to="/">Home</NavLink></li>
     <li>  <NavLink exact activeClassName="active_op" to="/about">About</NavLink></li>
     
{state?<><li><NavLink exact activeClassName="active_op" to="/login">Login</NavLink></li><li>  <NavLink exact activeClassName="active_op" to="/signup">Signup</NavLink></li></>:<><li><NavLink exact activeClassName="active_op" to="/logout">Logout</NavLink></li><li><NavLink exact activeClassName="active_op" to="/profile">Profile</NavLink></li></>}    
     
       </div>
     </ul>
   </nav>
    </>
}

export default Menu;