import React, { useState } from 'react'
import { useHistory } from 'react-router';
import userContext from './UserContext';
import { ActionCreators } from '../../store/index'
import { useDispatch } from 'react-redux';

const UserState = (props) => {

  const dispatch = useDispatch()

  const location = useHistory();
  const host = "http://localhost:5000"
  const login_func = async (email, password) => {
    const response = await fetch(`${host}/api/auth/login`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },

      body: JSON.stringify({ email, password })
    });
    const json = await response.json()
    if (json.success === true) {
      dispatch(ActionCreators.state_msg("User Login Successfully"))
      dispatch(ActionCreators.state_val(true))
      dispatch(ActionCreators.token(false))
      
      localStorage.setItem("token", json.authtoken)
      location.push("/")
    }
    else {
      dispatch(ActionCreators.state_msg("Please Try Again!!!With Correct Email And Password"))
      dispatch(ActionCreators.state_val(true))
    
    }

  }
  const signup_func = async (name, email, password) => {
    const response = await fetch(`${host}/api/auth/createuser`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },

      body: JSON.stringify({ name, email, password })
    });
    const json = await response.json()
    if (json.success === false) {

      dispatch(ActionCreators.state_msg(json.errors))
      dispatch(ActionCreators.state_val(true))
      location.push("/signup")
    }
    else {
      dispatch(ActionCreators.state_msg("User Has been created successfully"))
      dispatch(ActionCreators.state_val(true))
      location.push("/login")
      console.log(json.success)
    }
  }

  const profile_func = async()=>{
    console.log("profile")
    const response = await fetch(`${host}/api/auth/userdetail`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')
      },

    });
    const json = await response.json()
    dispatch(ActionCreators.name(json.user.name))
    dispatch(ActionCreators._id(json.user._id))
    dispatch(ActionCreators.email(json.user.email))
    dispatch(ActionCreators.date(json.user.date))

    console.log(json.user.date)
  }
  return (
    <div>
      <userContext.Provider value={{ login_func, signup_func ,profile_func}}>
        {props.children}
      </userContext.Provider>
    </div>
  )
}

export default UserState;