import React, { useContext } from 'react'
import NoteContext from '../../context/note/NoteContext'


function NewsItem(props) {

  const context = useContext(NoteContext)
  const { deleteNote } = context
  const { notes, update_notes, keys } = props


  return (<>
    <div className="col-md-3 my-3">
      <div className="card">
        <div className="card-body">
          <h5 className="card-title">{notes.title}</h5>
          <h6 className="card-subtitle mb-2 text-muted">{notes.tag}</h6>
          <p className="card-text">{notes.descrption}</p>

          <i className="far fa-trash-alt mx-3" onClick={() => { deleteNote(notes._id) }} ></i>
          <i className="fas fa-edit" onClick={() => { update_notes(notes) }}></i>
        </div>
      </div>
    </div>

  </>
  )
}

export default NewsItem
