import NoteContext from "./NoteContext";
import React from "react";
import { useState } from "react";
import { ActionCreators } from '../../store/index'
import { useDispatch } from "react-redux";

const NoteState = (props) => {
  const dispatch = useDispatch()
  const host = "http://localhost:5000"

  let s1 = [

  ]

  const [notes, setNotes] = useState(s1)

  const addNote = async (title, descrption, tag) => {
    console.log("add")
    const response = await fetch(`${host}/api/notes/createnote`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')

      },

      body: JSON.stringify({ title, descrption, tag })
    });

    const json = await response.json()

    setNotes(notes.concat(json))
    dispatch(ActionCreators.state_msg("Note Added Successfully"))
    dispatch(ActionCreators.state_val(true))

    console.log(json)

  }

  const getNotes = async () => {
    console.log("fetch all notes")
    const response = await fetch(`${host}/api/notes/fetchnotes`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')
      }

    });
    const json = await response.json();
    setNotes(json)
  }

  const deleteNote = async (id) => {
    const response = await fetch(`${host}/api/notes/deletenote/${id}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')
      }
    });
    dispatch(ActionCreators.state_msg(`Note with id${id} has been deleted`))
    dispatch(ActionCreators.state_val(true))
    const delnote = notes.filter((note) => { return note._id !== id })
    setNotes(delnote)
  }

  const edit_notes = async (id, title, descrption, tag) => {

    const response = await fetch(`${host}/api/notes/updatenote/${id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')
      },
      body: JSON.stringify({ title, descrption, tag })
    });
    const newNotes = JSON.parse(JSON.stringify(notes))
    dispatch(ActionCreators.state_msg(`Note Has been updated successfully`))
    dispatch(ActionCreators.state_val(true))
    for (let index = 0; index < newNotes.length; index++) {
      const note_s = newNotes[index];
      if (note_s._id === id) {
        newNotes[index].title = title;
        newNotes[index].descrption = descrption;
        newNotes[index].tag = tag;
        break;
      }


    }
    setNotes(newNotes)



  }

  return (
    <NoteContext.Provider value={{ notes, addNote, deleteNote, getNotes, edit_notes }}>
      {props.children}
    </NoteContext.Provider>
  )
}

export default NoteState;