import React, { useContext, useState } from 'react'
import userContext from '../context/user/UserContext';
import Alert from "../components/Alert"
import { useSelector } from 'react-redux';
const Login = () => {
    const [credtials, setcredials] = useState({ email: "", password: "" })
    const context = useContext(userContext)
    const { login_func } = context
    const handle = (e) => {
        e.preventDefault();

        login_func(credtials.email, credtials.password);
    }

    const onchange = (e) => {
        setcredials({ ...credtials, [e.target.name]: e.target.value })


    }
    const alert_msg = useSelector(state => state.alert_msg)
    const alert_val = useSelector(state => state.alert_val)
    

    return (

        <>
            <div className="container my-3">

                {alert_val ? <Alert message={alert_msg} /> : ""}

                <form onSubmit={handle}>
                    <div className="form-group">
                        <label htmlFor="email">Email address</label>
                        <input type="email" className="form-control" name="email" id="eamil" aria-describedby="emailHelp" placeholder="Enter email" onChange={onchange} />
                        <small id="email" className="form-text text-muted">We'll never share your email with anyone else.</small>
                    </div>
                    <div className="form-group">
                        <label htmlFor="password">Password</label>
                        <input type="password" className="form-control" id="exampleInputPassword1" name="password" placeholder="Password" onChange={onchange} />
                    </div>
                    <div className="my-2">
                        <button type="submit" className="btn btn-primary">Login</button>
                    </div>
                </form>
            </div>
        </>
    )
}

export default Login
