import { combineReducers } from "redux";
import Alert_Msg from "./Alert_Msg";
import Alert_Redusers from "./Alert_Redusers";
import Token_Reduser from "./Token_Reduser";
import _id from './_id'
import name from './name'
import email from './email'
import date from './date'



const Redusers = combineReducers({
    alert_val:Alert_Redusers,
    alert_msg:Alert_Msg,
    token : Token_Reduser,
    _id:_id,
    name:name,
    email:email,
    date:date
})

export default Redusers